import React from "react";

import "./SocialMediaReelsPointer.css";

import { useInView } from "react-intersection-observer";

export default function SocialMediaReelsPointers() {
  const [refBox1a, inViewBox1a] = useInView({
    triggerOnce: true,
    rootMargin: "0px 0px",
  });

  const [refBox1b, inViewBox1b] = useInView({
    triggerOnce: true,
    rootMargin: "0px 0px",
  });

  const [refBox1c, inViewBox1c] = useInView({
    triggerOnce: true,
    rootMargin: "0px 0px",
  });

  const [refBox1d, inViewBox1d] = useInView({
    triggerOnce: true,
    rootMargin: "0px 0px",
  });

  const [refBox1e, inViewBox1e] = useInView({
    triggerOnce: true,
    rootMargin: "0px 0px",
  });

  const [refBox1f, inViewBox1f] = useInView({
    triggerOnce: true,
    rootMargin: "0px 0px",
  });

  return (
    <div className="social-media-reels-pointers-main-container">
      <h1>Why do you need social media reels for your brand?</h1>

      <h3
        ref={refBox1a}
        className={` ${
          inViewBox1a ? "social-media-reels-pointers-animations" : ""
        }`}
      >
        1. Increased Engagement
      </h3>

      <h3
        ref={refBox1b}
        className={` ${
          inViewBox1b ? "social-media-reels-pointers-animations" : ""
        }`}
      >
        2. Reach a Wider Audience
      </h3>

      <h3
        ref={refBox1c}
        className={` ${
          inViewBox1c ? "social-media-reels-pointers-animations" : ""
        }`}
      >
        3. Showcasing Products and Services
      </h3>

      <h3
        ref={refBox1d}
        className={` ${
          inViewBox1d ? "social-media-reels-pointers-animations" : ""
        }`}
      >
        4. Building Brand Personality
      </h3>

      <h3
        ref={refBox1e}
        className={` ${
          inViewBox1e ? "social-media-reels-pointers-animations" : ""
        }`}
      >
        5. Riding the Trend
      </h3>

      <h3
        ref={refBox1f}
        className={` ${
          inViewBox1f ? "social-media-reels-pointers-animations" : ""
        }`}
      >
        6. Viral Potential
      </h3>
    </div>
  );
}
